import Vue from 'vue'
import VueRouter from 'vue-router'
import set_titles from './Middlewares/title';
import check_auth  from './Middlewares/auth';
import check_guest  from './Middlewares/guest';
import routes from '../routes';
import config from '../config.json';

Vue.use(VueRouter);

/**
 * Router.
 */
const router = new VueRouter({
    //mode: 'history',
    base: process.env.BASE_URL,
    //base: __dirname,
    routes
});

/**
 * Adicionar controle de middlewares.
 */
check_auth(router, 'login');
check_guest(router, 'home');
set_titles(router, config.empresa.nome);

export default router;


