<template>
    <div class="row">
        <div class="col-xs-6 col-sm-6 col-md-6 alc-xs">NetForce Sistemas</div>
        <div class="col-xs-6 col-sm-6 col-md-6 text-right alc-xs">
            <small>{{ inicio }} - {{ anoAtual }}</small>
        </div>
    </div>
</template>

<script>

import config from "../../config.json";

export default {
    data() {
        return {
            inicio: config.empresa.fundacao,
        }
    },
    computed: {
        anoAtual: () => {
            return (new Date()).getUTCFullYear();
        }
    }
};
</script>


