import Vue from 'vue'
import VueI18n from 'vue-i18n';
const messages  = require('../Langs');
const config    = require('../config.json');

Vue.use(VueI18n);

const langs = new VueI18n({
    locale: config.app.locale,
    fallbackLocale: config.app.localefallback,
    messages,
});

export default langs;


