import CryptoJS from "crypto-js";
import config from "../config.json";

class Store
{
    /**
     * Verifica se existe.
     * 
     * @param {string} key Nome do campo no localStorage
     * @return bool
     */
    static has(key) {
        let value = localStorage.getItem(key);
        return (value);
    };

    /**
     * Recuperar um valor no store.
     * 
     * @param {string} key Nome do campo no localStorage
     * @param {mixed} def Valor padrao
     * @return mixed|string|null
     */
    static get(key, def = null) {
        let value = localStorage.getItem(key);

        if (value) {
            var bytes = CryptoJS.AES.decrypt(value.toString(), config.app.key);
            value = bytes.toString(CryptoJS.enc.Utf8);
        }

        return value ? value : def;
    };

    /**
     * Guardar um valor no store.
     * 
     * @param {string} key Nome do campo no localStorage
     * @param {mixed} value Valor a guardar
     */
    static set(key, value) {

        var encrypted = CryptoJS.AES.encrypt(value, config.app.key);

        localStorage.setItem(key, encrypted);
    };

    /**
     * Remover valor no store.
     * 
     * @param {string} key Nome do campo no localStorage
     */
    static remove(key) {
        localStorage.removeItem(key);
    };
};

export default Store;


