/**
 * Controle de rotas NAO autenticadas.
 */

import auth from '../auth';

export default (router, guestName) => {

    router.beforeEach(async (to, from, next) => {
        // Verificar se rota deve estar autenticada.
        // Caso nao necessite, continuar fluxo.
        if (!to.meta.guest) {
            return next();
        }

        // Verificar se contexto esta autenticado
        if (await auth.check()) {
            return router.push({ name: guestName });
        }

        return next();
    });

};


