<template>
    <li class="dropdown" v-show="isLogado">
        <a
            aria-expanded="false"
            role="button"
            href="#"
            class="dropdown-toggle menu-user"
            data-toggle="dropdown"
        >
            <img
                src="https://cdn.jsdelivr.net/gh/netforcews/theme/imgs/avatar.jpg"
                class="img-thumbnail rounded-circle avatar"
            >
        </a>
        <ul role="menu" class="dropdown-menu">
            <p class="p-4 m-b-sm">
                <strong>{{ user.nome }}</strong>
                <br>
                <small>{{ user.email }}</small>
            </p>
            <li class="dropdown-divider"></li>
            <li>
                <a href>Configuraes do usurio</a>
            </li>
            <li>
                <a href>Configuraes do sistema</a>
            </li>
            <li class="dropdown-divider"></li>
            <li>
                <a v-on:click="logout">{{ $t('auth.logout') }}</a>
            </li>
        </ul>
    </li>
</template>

<script>
import auth from "../../App/auth";

export default {
    mounted: function() {
        auth.user().then((user) => {
            this.user = user;            
            this.isLogado = true;
        });
    },

    data() {
        return {
            user: {
                nome: '',
                email: '',
            },
            isLogado: false,
        };
    },

    methods: {
        logout: function() {
            auth.logout().then(() => {
                this.$router.push({ name: "login" });
            });
        }
    }
};
</script>


