<template>
    <div id="error">
        <div class="middle-box text-center">
            <slot></slot>
            <hr>
            <p>
                <b>{{ code }}</b> Ops! {{ message }}
            </p>
        </div>
    </div>        
</template>

<script>

export default {
    name: "error",

    props: {
        code: {
            type: String,
            required: true,
        },
        message: {
            type: String,
            required: true,
        }
    },

    data() {
        return {};
    }
};
</script>


