<template>
    <div class="footer">
        <div class="float-right">
            10GB of
            <strong>250GB</strong> Free.
        </div>
        <div>
            <strong>Copyright</strong> {{ empresa }} &copy; {{ inicio }} - {{ anoAtual }}
        </div>
    </div>
</template>

<script>
import config from "../../config.json";

export default {
    data() {
        return {
            inicio: config.empresa.fundacao
        };
    },
    computed: {
        anoAtual: () => {
            return new Date().getUTCFullYear();
        },

        empresa: () => {
            return config.empresa.nome;
        }
    }
};
</script>


