//import nws from 'nws-sdk/dev';
import Store from './store';

class Auth
{
    /**
     * Construtor.
     */
    constructor()
    {
        this.$token = null;
        this.$user  = null;

        //nws.dev();

        this.$auth = new nws.auth();
    }

    /**
     * Verificar se usurio j esta logado.
     * 
     * @returns {Boolean}
     */
    async check()
    {
        return (await this.user() != null);
    }

    /**
     * Retorna o usuario logado.
     * 
     * @returns {Object|null}
     */
    async user()
    {
        // Verificar se usuario j foi carregado
        if (this.$user) {
            return this.$user;
        }

        // Carregar token de acesso
        var token = this.__getToken();
        if ((!token) || (token == '')) {
            return null;
        }

        // Informar access token para o sdk
        nws.setAccessToken(token);

        // Carregar dados do usuario
        try {
            return this.$user = await this.$auth.me();
        } catch (err) {
            this.__clearCache();
            return null;
        }
    }
    
    /**
     * Fazer o login.
     * 
     * @param {String} email E-mail do login
     * @param {String} senha Senha do login
     * @returns {Object}
     */
    async login(email, senha)
    {
        var ret = await this.$auth.login(email, senha);

        this.$user = null;
        this.setToken(ret.access_token);
        
        return this.user();
    }

    /**
     * Executar o logout.
     * 
     * @returns {Boolean}
     */
    async logout()
    {
        try {
            await this.$auth.logout();
        } catch (er) {
            // ignorar....
        }

        this.$user = null;
        this.setToken(null);

        return true;
    }

    /**
     * Guardar token de acesso.
     * 
     * @param {String} token Access token
     * @returns {Auth}
     */
    setToken(token)
    {
        this.$token = token;

        Store.set('nf.auth.token', token);

        return this;
    }

    /**
     * Retorna o access token local.
     * 
     * @returns {String}
     */
    __getToken()
    {
        // Verificar se ja foi carregado
        if (this.$token) {
            return this.$token;
        }

        return this.$token = Store.get('nf.auth.token');
    }

    /**
     * Limpar cache da sessao.
     */
    __clearCache()
    {
        this.$token = null;
        this.$user = null;
        nws.setAccessToken(null);
    }
}

export default new Auth;


