<template>
    <div class="form-login">
        <div class="m-b-lg">
            <router-link to="/auth/login">
                <img src="https://cdn.jsdelivr.net/gh/netforcews/assets/imgs/logo_login.png">
            </router-link>
        </div>

        <h2>{{ $t('auth.login.title') }}</h2>

        <div class="row">
            <div class="col-8">
                <div class="alert alert-danger alert-dismissable alc" v-if="message">{{ message }}</div>

                <form class="m-t" role="form" v-on:submit="login">
                    <div class="form-group">
                        <input
                            type="email"
                            v-model="email"
                            class="form-control"
                            v-bind:placeholder="$t('auth.login.email')"
                            v-on:change="clearMessage()"
                            ref="email"
                            required
                        >
                    </div>
                    <div class="form-group">
                        <input
                            type="password"
                            v-model="senha"
                            class="form-control"
                            v-bind:placeholder="$t('auth.login.password')"
                            v-on:change="clearMessage()"
                            ref="senha"
                            required
                        >
                    </div>

                    <button
                        type="submit"
                        class="btn btn-primary block full-width m-b ladda-button"
                        data-style="expand-left"
                        data-size="xs"
                    >{{ $t('auth.login.entrar') }}</button>

                    <router-link to="/auth/remember">{{ $t('auth.login.esqueci') }}</router-link>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
import * as Ladda from 'ladda';
import auth from '../../App/auth';
import store from '../../App/store';

export default {
    data() {
        return {
            email: "",
            senha: "",
            message: ""
        };
    },

    mounted: async function() {
        this.email = store.get('nf.auth.last_email', '');

        // Verificar qual campo focar
        if (this.email == "") {
            this.$refs.email.focus();
        } else {
            this.$refs.senha.focus();
        }

        // Verificar se ja esta logado
        if (await auth.check()) {
            this.goContinue();
        }
    },

    methods: {
        login: async function(event) {
            event.preventDefault();
            this.message = "";

            var l = Ladda.create(this.$el.querySelector('.ladda-button'));
            l.start();

            auth.login(this.email, this.senha).then((user) => {

                // Guardar ultimo e-mail
                store.set('nf.auth.last_email', this.email);

                // Verificar qual url de destino
                this.goContinue();

                l.stop();
            }).catch((err) => {
                this.message = err.message;
                l.stop();
            });
        },

        clearMessage: function() {
            this.message = "";
        },

        goContinue: function() {
            var _continue = this.$route.query.continue;

            // Verificar se foi informado um nome de rota
            if ((_continue) && (_continue.indexOf('http') < 0)) {
                _continue = { name: _continue };
            }

            // Redirecionar para url.
            if (_continue) {
                if (typeof _continue == 'string') {
                    window.location = _continue;
                } else {
                    this.$router.push(_continue);
                }
            } else {
                this.$router.push({ name: 'home' });
            }
        }
    }
};
</script>


<style lang="less">
@import "../../../node_modules/ladda/dist/ladda-themeless.min.css";
</style>


