<template>
    <div id="wrapper">
        <div id="page-wrapper" class="gray-bg netforce-bg">
            <div class="row border-bottom white-bg">
                <!-- header top -->
                <nav class="navbar navbar-expand-lg navbar-static-top" role="navigation">
                    <router-link v-bind:to="urlHome" class="navbar-brand">
                        <i v-bind:class="icon"></i>
                        |
                        {{ title }}
                    </router-link>

                    <button
                        class="navbar-toggler"
                        type="button"
                        data-toggle="collapse"
                        data-target="#navbar"
                        aria-expanded="false"
                        aria-label="Toggle navigation"
                    >
                        <i class="fas fa-bars"></i>
                    </button>

                    <div class="navbar-collapse collapse" id="navbar">
                        <ul class="nav navbar-nav mr-auto">
                            <li v-for="menu in menus">
                                <router-link v-bind:to="menu.path">{{ menu.label }}</router-link>
                            </li>
                        </ul>
                        <ul class="nav navbar-right navbar-top-links">
                            <menu-top-company></menu-top-company>
                            <menu-top-apps></menu-top-apps>
                            <menu-top-user></menu-top-user>
                        </ul>
                    </div>
                </nav>
            </div>

            <!-- main -->
            <div class="wrapper wrapper-content">
                <div class="container-fluid">
                    <slot></slot>
                </div>
            </div>

            <!-- footer -->
            <footer-main></footer-main>
        </div>
    </div>
</template>

<script>
import MenuTopApps    from "./Controls/MenuTopApps.vue";
import MenuTopCompany from "./Controls/MenuTopCompany.vue";
import MenuTopUser    from "./Controls/MenuTopUser.vue";
import FooterMain     from "./Controls/FooterMain.vue";

export default {
    name: "wrapper",

    components: {
        MenuTopApps,
        MenuTopCompany,
        MenuTopUser,
        FooterMain
    },

    props: {
        title: {
            type: String
        },

        icon: {
            type: String
        },

        urlHome: {
            type: String,
            default: ""
        },

        menus: {
            type: Array,
            default: () => {
                return [];
            }
        }
    },

    data() {
        return {};
    }
};
</script>


