import Page404 from './Views/errors/404.vue';
import PageLogin from './Views/auth/Login.vue';
import PageHome from './Views/home/Home.vue';

export default [
    // Home Alias
    {
        path: '/',
        redirect: { name: 'home' }
    },
    // 404
    {
        path: '*',
        component: Page404,
        meta: {
            title: '404'
        }
    },
    // Login
    {
        path: '/auth/login',
        name: 'login',
        component: PageLogin,
        meta: {
            title: 'Login'
        }
    },
    // Home
    {
        path: '/home',
        name: 'home',
        component: PageHome,
        meta: {
            title: 'Home',
            auth: true,
        }
    }
];


