<template>
    <li class="dropdown">
        <a
            aria-expanded="false"
            role="button"
            href="#"
            class="dropdown-toggle nav-icon-only"
            data-toggle="dropdown"
            v-bind:title="$t('apps.title')"
        >
            <i class="fas fa-th"></i>
        </a>
        <div role="menu" class="dropdown-menu dropdown-apps">
            <div class="row">
                <div class="col col-6 app" v-for="item in ['Pessoas','Produtos','Vendas','Compras','PCP','WMS','Financeiro','Tabelas','Admin']">
                    <a href="#">
                        <i class="icon fas fa-rocket"></i>
                        <span>{{ item }}</span>
                    </a>
                </div>
            </div>
        </div>
    </li>
</template>

<script>
export default {
    
};
</script>

<style lang="less" scoped>
@import '../Assets/less/variables.less';

.dropdown-apps {
    width: 300px;
    padding: 0px 15px;
    max-height: 448px;
    overflow-y: auto;

    div.app {
        text-align: center;
        padding: 5px;

        > a {
            display: block;

            &:hover {
                background-color: @light-gray;
            }


            > i.icon {
                font-size: 36px;
                color: @navy;
            }

            >  span {
                display: block;
                color: @text-color;
                margin-top: 7px;
            }
        }
    }
}
</style>



